import React from 'react';
import data from '../data/data.json';
import img1 from '../img/xanana.jpg';
import img2 from '../img/uma_lisan.jpeg';
import img3 from '../img/nain_feto_rm.jpg';
import img4 from '../img/ai_pelo.jpg';
import img5 from '../img/ramos_horta.jpg';
import img6 from '../img/capital_dili.jpg';
import img7 from '../img/papa_ii.jpg';
import img8 from '../img/tebe_dai.jpg';


function SomeImg(props) {
  let imgSrc;
  props.myQuestion === 0 ? imgSrc = img1 :
  props.myQuestion === 1 ? imgSrc = img2 :
  props.myQuestion === 2 ? imgSrc = img3 : 
  props.myQuestion === 3 ? imgSrc = img4 :
  props.myQuestion === 4 ? imgSrc = img5 :
  props.myQuestion === 5 ? imgSrc = img6 :
  props.myQuestion === 6 ? imgSrc = img7 :
  props.myQuestion === 7 ? imgSrc = img8 :
  imgSrc = null
  return <img className="app-photo" src={imgSrc} alt={imgSrc} />;
}


function MyForm(props) {
  return (
    <>
    {
    props.myQuestion < data.length &&
    <form onSubmit={props.onHandledSubmit}>
    <label className="question">{data[props.myQuestion].question}</label>
    {
      Object.keys(data[props.myQuestion].answer).length > 0 &&
      Object.keys(data[props.myQuestion].answer).map((value, index) => <label key={index.toString()}>
          <input type="radio" className="rads" name="answer" value={value} onChange={props.onHandledChange} required={true} autoFocus={true} />
          {value}
        </label>)
    }
    <input className="button" type="submit" value="next" />
  </form>
  }
  </>);
}


function ShowScore(props) {
  const myStyle = {
    fontWeight: "bold",
    color: props.information
  }
  return (
    <>
      {
        props.playerScore > -1 &&
        <p style={myStyle}>Hau nia Valor: {props.playerScore}</p>
      } 
    </>
  )
}


function FinalResult(props) {
  return (
    <>
      {
          props.myQuestion > data.length-1 &&
          <div className="yourscore">
            {
              props.playerScore > 40 
              ? <>
                  <h3>Parabens!</h3>
                  <p >Ita nia valor {props.playerScore}!</p>
                </>
              : <>
                  <h3>Valor laduun diak!</h3>
                  <p style={{color: 'red'}}>Ita nia valor {props.playerScore}!</p>
                </>
            }

            <button className="button" onClick={props.onPrevApp}>Fila...</button>
          </div>
        }
    </>
  )
}



class Main extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      text: '',
      question: 0,
      score: 0,
      information: null
    };
    this.handledChange = this.handledChange.bind(this);
    this.handledSubmit = this.handledSubmit.bind(this);
  }

  handledChange(event) {
    this.setState({text: event.target.value});
  }

  handledSubmit(event) {
    event.preventDefault();
    // console.log(this.state.question + ": " + this.state.text);
    // console.log(data[this.state.question].answer[`${this.state.text}`]);
    this.countScore(data[this.state.question].answer[`${this.state.text}`]);
    this.setState(state => ({text: '', question: state.question + 1}));
  }

  countScore(answer) {
    if(answer === 'loos') {
      this.setState(state => ({score: state.score + 12.5}));
      this.setState({information: '#1abc9c'});
    } else {
      this.setState({information: 'red'});
    }
  }

  render() {

    return (
      <div className="main-app">

        <SomeImg myQuestion={this.state.question} />

        <MyForm 
          onHandledChange={this.handledChange} 
          onHandledSubmit={this.handledSubmit}
          myQuestion={this.state.question} />

        <ShowScore playerScore={this.state.score} information={this.state.information} />

        <FinalResult 
          myQuestion={this.state.question}
          playerScore={this.state.score} onPrevApp={this.props.prevApp} />

      </div>
    )
  }
}

export default Main;