import React from 'react';
import Welcome from './Welcome';
import Main from './Main';
import '../css/mystyle.css';

class ControlApp extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      app: 0
    };
    this.prevApp = this.prevApp.bind(this);
    this.nextApp = this.nextApp.bind(this);
  }

  nextApp() {
    this.setState(state => ({
      app: state.app + 1
    }));
  }

  prevApp() {
    this.setState(state => ({
      app: state.app - 1
    }));
  }

  render() {

    let runApp = this.state.app === 0 
                ? <Welcome nextApp={this.nextApp} />
                : <Main prevApp={this.prevApp} /> ;

    return (
      <>
        {runApp}
      </>
    )
  }

}

export default ControlApp;